import React, { useState } from 'react';
import Product from './components/comps/product';

import css from "./styles/product.css";

const { ProductContainer } = css;

function App() {
  const [cart, setCart] = useState({"Tier 1": 0, "Tier 2": 0, "Tier 3": 0});

  const showCart = () => {
    if (cart["Tier 1"] + cart["Tier 2"] + cart["Tier 3"] === 0) {
      return <></>
    } else {
      return <div>
        <h1>Cart</h1>
        <h3>Total: {Math.round((cart["Tier 1"] * 0.3 + cart["Tier 2"] * 0.2 + cart["Tier 3"] * 0.1) * 100) / 100} TON</h3>
        <button onClick={() => alert("Thank you for your purchase!")}>Purchase</button>
        <br/>
        <button onClick={() => setCart({"Tier 1": 0, "Tier 2": 0, "Tier 3": 0})}>Clear Cart</button>
      </div>
    }

  }

  return (
    <React.Fragment>
      <ProductContainer>
        <Product productName={"Tier 1"} productPrice={0.3} quantity={cart["Tier 1"]} setCart={setCart}/>
        <Product productName={"Tier 2"} productPrice={0.2} quantity={cart["Tier 2"]} setCart={setCart}/>
        <Product productName={"Tier 3"} productPrice={0.1} quantity={cart["Tier 3"]} setCart={setCart}/>
      </ProductContainer>
      {showCart()}
    </React.Fragment>
  );
}

export default App;
