import React from "react";

const Product = (props) => {
    function addToCart() {
        props.setCart((prevCart) => ({
          ...prevCart,
          [props.productName]: (prevCart[props.productName] || 0) + 1,
        }));
      };

    return (
        <>
            <div className="product" style={{margin: "10px"}}>
                <div className="product__photo">
                    <img src="https://via.placeholder.com/200x150" alt="Product" />
                </div>
                <div className="product__info">
                    <div className="product__name">{props.productName}</div>
                    <div className="product__price">Price: {props.productPrice} TON</div>
                    <div className="product__quantity">Quantity: {props.quantity}</div>
                    <div className="product__button">
                        <button onClick={addToCart}>Add to Cart</button>
                    </div>
                </div>
            </div>
        </>
    );
    };


export default Product;
